@import "./variables.scss";

.logo-footer {
  padding-bottom: 30px;

  @media (max-width: $vp-767) {
    display: flex;
    justify-content: center;
    padding-bottom: 15px;
  }
}

.logo-footer__link {
  display: flex;
}

.logo-footer__image {
  margin-right: 15px;
}

.logo-footer__heading-wrapper h2 {
  margin: 0;

  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: $color-default-white;

  cursor: default;
}

.logo-footer__heading-wrapper p {
  margin: 0;

  font-family: $ff-roboto;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: $transparent-white-0-7;

  cursor: default;
}
