@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f2f5f6;
}

a {
  color: #f2f5f6;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f2f5f6;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  background-color: #041827;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}

.main-header {
  position: fixed;
  width: 100%;
  padding: 30px 0 35px 0;
  background: linear-gradient(to bottom, #041827 85%, rgba(255, 255, 255, 0) 100%);
  z-index: 1;
}
@media (max-width: 1023px) {
  .main-header {
    position: static;
    padding: 20px 0 25px 0;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding: 10px 0 20px 0;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1380px;
  margin: 0 auto;
  padding: 0 80px;
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    justify-content: flex-end;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    padding: 0 25px;
  }
}

@media (max-width: 1023px) {
  .main-header__logo-wrapper {
    margin-right: auto;
  }
}

.main-header__logo {
  display: flex;
  width: 100%;
  margin: 0;
  padding: 0;
}

.main-header__heading-wrapper {
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-header__heading-wrapper {
    display: none;
  }
}

.main-header__heading {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #f2f5f6;
  cursor: default;
}

.main-header__heading-small {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.65);
  cursor: default;
}

@media (max-width: 1023px) {
  .main-header__email-wrapper,
.main-header__phone-wrapper {
    width: 40px;
    height: 40px;
    margin-right: 20px;
  }
}

@media (max-width: 767px) {
  .main-header__phone-wrapper {
    margin-right: 10px;
  }
}

.main-header__email-link,
.main-header__phone-link {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 15px;
}
@media (max-width: 1023px) {
  .main-header__email-link,
.main-header__phone-link {
    width: 100%;
    height: 100%;
  }
}

.main-header__email-text,
.main-header__phone-text {
  margin: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .main-header__email-text,
.main-header__phone-text {
    display: none;
  }
}

.logo-header__image {
  margin-right: 15px;
}
.logo-header__image--desktop-and-tablet {
  display: block;
}
@media (max-width: 767px) {
  .logo-header__image--desktop-and-tablet {
    display: none;
  }
}
.logo-header__image--mobile {
  display: none;
}
@media (max-width: 767px) {
  .logo-header__image--mobile {
    display: block;
  }
}

.main-container {
  display: grid;
  margin-bottom: auto;
}

.promo {
  min-height: 640px;
  background: #041827 url("../img/backgrounds/bg-image-promo-desktop.jpg") no-repeat bottom;
  background-size: cover;
}
@media (min-width: 1023px) and (min-resolution: 144dpi), (min-width: 1023px) and (min-resolution: 1.5dppx) {
  .promo {
    background: #041827 url("../img/backgrounds/bg-image-promo-desktop@2x.jpg") no-repeat bottom;
    background-size: cover;
  }
}
@media (max-width: 1023px) {
  .promo {
    min-height: 560px;
    margin-top: -100px;
    background: #041827 url("../img/backgrounds/bg-image-promo-tablet.jpg") no-repeat bottom;
    background-size: cover;
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .promo {
    background: #041827 url("../img/backgrounds/bg-image-promo-tablet@2x.jpg") no-repeat bottom;
    background-size: cover;
  }
}
@media (max-width: 767px) {
  .promo {
    min-height: auto;
    margin-top: -70px;
    background: #041827 url("../img/backgrounds/bg-image-promo-mobile.jpg") no-repeat bottom;
    background-size: cover;
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .promo {
    background: #041827 url("../img/backgrounds/bg-image-promo-mobile@2x.jpg") no-repeat bottom;
    background-size: cover;
  }
}

.promo__wrapper {
  max-width: 1380px;
  margin: 15% auto 15.6% auto;
  padding: 0 75px;
}
@media (max-width: 1023px) {
  .promo__wrapper {
    margin: 23.5% auto 28.1% auto;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .promo__wrapper {
    margin: 48% auto 57.5% auto;
    padding: 0 25px;
  }
}

.promo__container {
  margin-left: 10%;
}
@media (max-width: 1023px) {
  .promo__container {
    margin-left: 0;
  }
}

.promo h2 {
  margin: 0;
  padding-bottom: 40px;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  color: #f2f5f6;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  cursor: default;
}
@media (max-width: 1023px) {
  .promo h2 {
    padding-bottom: 0;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .promo h2 {
    padding-bottom: 10px;
    font-size: 36px;
    line-height: 42px;
  }
}

.promo p {
  max-width: 685px;
  margin: 0;
  padding-bottom: 40px;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #f2f5f6;
  cursor: default;
}
@media (max-width: 1023px) {
  .promo p {
    padding-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .promo p {
    font-size: 14px;
    line-height: 21px;
  }
}

.promo a {
  margin: 0;
  padding: 15px 25px;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #f2f5f6;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: #00b2ff;
  border-radius: 3px;
}
@media (max-width: 1023px) {
  .promo a {
    margin-left: 6%;
  }
}
@media (max-width: 767px) {
  .promo a {
    margin-left: 0;
    padding: 10px 30px;
    font-size: 14px;
    line-height: 17px;
  }
}

.promo__link-text--long {
  display: block;
}
@media (max-width: 767px) {
  .promo__link-text--long {
    display: none;
  }
}
.promo__link-text--short {
  display: none;
}
@media (max-width: 767px) {
  .promo__link-text--short {
    display: block;
  }
}

.advantages {
  padding: 120px 0;
  background-color: #ffffff;
}
@media (max-width: 1023px) {
  .advantages {
    padding: 80px 0;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding: 50px 0;
  }
}

.advantages__wrapper {
  max-width: 1380px;
  margin: 0 auto;
  padding: 0 80px;
}
@media (max-width: 1023px) {
  .advantages__wrapper {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper {
    padding: 0 25px;
  }
}

.advantages h3 {
  margin: 0;
  padding-bottom: 15px;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
  cursor: default;
}
@media (max-width: 1023px) {
  .advantages h3 {
    line-height: 25px;
  }
}
@media (max-width: 767px) {
  .advantages h3 {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages p {
  min-height: 120px;
  margin: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
  cursor: default;
}
@media (max-width: 767px) {
  .advantages p {
    min-height: 140px;
    font-size: 14px;
    line-height: 19px;
  }
}

.advantages ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .advantages ul {
    grid-template-columns: 1fr;
  }
}

.advantages li {
  position: relative;
  padding: 40px 45px 35px 45px;
  outline: 1px solid #ebebeb;
}
.advantages li:hover {
  background-color: #00b2ff;
}
.advantages li:hover h3, .advantages li:hover p {
  color: #f2f5f6;
}
.advantages li:hover svg {
  fill: #f2f5f6;
}
.advantages li::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 5px;
  height: 100%;
  background-color: #00b2ff;
}
@media (max-width: 1023px) {
  .advantages li {
    padding: 40px 30px 35px 45px;
  }
}
@media (max-width: 767px) {
  .advantages li {
    padding: 30px 30px 35px 30px;
  }
}

.advantages svg {
  margin-bottom: 20px;
  fill: #00b2ff;
}
@media (max-width: 767px) {
  .advantages svg {
    width: 30px;
    height: 30px;
  }
}

.about-us {
  margin-top: -1px;
  background-color: #ffffff;
}

