@media (min-width: 1024px) and (min-resolution: 144dpi), (min-width: 1024px) and (min-resolution: 1.5dppx) {
  .about-us__wrapper::before {
    background-image: url("../img/backgrounds/bg-image-2022-desktop-and-tablet@2x.png");
    background-size: cover;
  }
}
@media (max-width: 1023px) {
  .about-us__wrapper::before {
    left: 50px;
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .about-us__wrapper::before {
    background-image: url("../img/backgrounds/bg-image-2022-desktop-and-tablet@2x.png");
    background-size: cover;
  }
}
@media (max-width: 767px) {
  .about-us__wrapper::before {
    display: none;
  }
}

@media (max-width: 767px) {
  .about-us__spoiler-content--first {
    display: none;
  }
}
.about-us__spoiler-content--second, .about-us__spoiler-content--third {
  display: none;
}

.about-us h2 {
  margin: 0;
  padding: 5px 0 15px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 50px;
  color: #041827;
  cursor: default;
}
@media (max-width: 1023px) {
  .about-us h2 {
    padding: 0 0 10px 0;
